import {createStore} from 'vuex'

// const configFile = require("./config");

export default createStore({
    state() {
        return {
            finalDate: 1714435500,
            // finalDate: (Date.now() / 1000) + (16 * 60),
            heading: "meeting her 💜",
            inputState: 0,
            alerts: {}
        }
    },

    getters: {
        finalDateConv(state) {
            return state.finalDate * 1000;
        },
        inputStateConv(state) {
            let days = Math.floor(state.inputState / 86400);
            let diff = new Date(state.inputState * 1000);
            let hours = diff.getUTCHours();
            let minutes = diff.getUTCMinutes();
            let seconds = diff.getUTCSeconds();
            return {total: state.inputState, days: days, hours: hours, minutes: minutes, seconds: seconds};
        }
    },

    mutations: {
        setInput(state, payload) {
            if (payload === '') {
                state.inputState = 0;
            } else {
                state.inputState = payload;
            }
        },
        addAlert(state, payload) {
            state.alerts[payload.total] = payload;
        },
        clearAlert(state, key) {
            delete state[key];
        }
    },

    actions: {

    },
    modules: {}
})
