<script>
export default {
  name: "inputField",
  props: [],
  data() {
    return {
      val: ''
    }
  },
  methods: {
    clearVal() {
      this.val = '';
    }
  },
  watch: {
    'val'() {
      this.$store.commit("setInput", this.val);
    }
  }
}
</script>

<template>
  <input type="number" id="inp" placeholder="Enter seconds" v-model="val"
         @keydown.enter="$emit('enterPress');this.clearVal()"/>
</template>

<style scoped lang="sass">
@import "@/assets/variables.sass"

#inp
  font-size: 2rem
  border: none
  background: none

  text-align: center
  font-family: $base-font-bold
  margin-bottom: 0.5rem

#inp::-webkit-inner-spin-button
  display: none

#inp:focus
  outline-width: 0

</style>