<template>

  <div id="countdown">

    <div id="percentage">
      {{ seconds }}
    </div>

    <div id="title">
      total seconds on this site
    </div>

  </div>

</template>

<script>

import circularLoadingBar from "@/components/circularLoadingBar.vue";

export default {
  props: [],
  name: "beenOnTheSite",
  components: {circularLoadingBar},
  data() {
    return {
      seconds: 0
    }
  },
  computed: {

  },

  methods: {

  },

  mounted() {

    window.addEventListener("beforeunload", () => {
      localStorage.setItem("secondsOnTheSite", this.seconds);
    })

    let config = localStorage.getItem("secondsOnTheSite");
    if (!config) {
      this.seconds = 0;
    } else{
      this.seconds = config;
    }

    let mainLoop = setInterval(() =>{
      this.seconds++;
    }, 1000);
  },
}

</script>

<style lang="sass" scoped>
@import "../assets/variables.sass"

#countdown
  width: 10rem
  display: flex
  flex-direction: column

  align-items: center

  border: 2px solid $base-button-border
  border-radius: 10px

  box-shadow: $base-shadow
  margin: 1rem

  backdrop-filter: blur(1rem)

  font-size: 1.4rem

#percentage
  padding-top: 2.5rem
  text-align: center

#title
  font: $base-font
  color: $base-font-color

  text-align: center

</style>