<template>

  <div id="countdown">

    <div id="percentage">
      <circular-loading-bar :percentage="percentage"/>
    </div>

    <div id="percentage-text">
      {{percentage}} %
    </div>

    <div id="text-wrapper">
      <div id="cur">{{ this.cur }}</div>
      <div id="description">{{ this.title }}</div>
    </div>

  </div>

</template>

<script>

import circularLoadingBar from "@/components/circularLoadingBar.vue";

export default {
  props: ["cur", "until", "title", "mode"],
  name: "countdown",
  components: {circularLoadingBar},
  data() {
    return {

    }
  },
  computed: {
    percentage() {

      let current;
      if (this.cur === null) {
        current = 0;
      } else {
        current = this.cur;
      }

      return Math.floor((current / this.until) * 100);
    },
  },

  methods: {

  },

  watch: {

  },
}

</script>

<style lang="sass" scoped>
@import "../assets/variables.sass"

#countdown
  display: grid

  grid-template-rows: 1fr auto 1fr
  grid-template-columns: auto 1fr auto

  place-items: center

#percentage
  grid-row: 1
  grid-column: 2
  text-align: center

#percentage-text
  grid-row: 1 / 2
  grid-column: 2
  font-size: 2rem
  padding-top: 5rem
  
#text-wrapper
  grid-row: 3
  grid-column: 2

  display: flex

#cur, #description

  text-align: center
  font-size: 2rem

  padding: 0 0.5rem 0 0.5rem

#cur
  font-family: $base-font-bold

#description
  font-family: $base-font-regular
  cursor: text

</style>