<template>

  <div id="body">

    <div id="title">
      <h1>{{this.$store.state.heading}}</h1>
    </div>

    <div id="total">
      <numbertext :number="this.tts" text="seconds"/>
    </div>

    <!--
    <div id="inpf">
      <input-field @enter-press="this.logState()"/>
    </div>
    -->

    <div id="days">
      <countdown :cur="this.days" :until="356" :title="'days'" :mode="null"/>
    </div>

    <div id="hours">
      <countdown :cur="this.hours" :until="24" :title="'hours'" :mode="null"/>
    </div>

    <div id="minutes">
      <countdown :cur="this.minutes" :until="60" :title="'minutes'" :mode="null"/>
    </div>

    <div id="seconds">
      <countdown :cur="this.seconds" :until="60" :title="'seconds'" :mode="null"/>
    </div>

    <div id="bots">
      <numbertext :number="this.secondsOnSite" text="total seconds on this site"/>
    </div>

  </div>

</template>

<script>
import countdown from "@/components/countdown.vue";
import Total from "@/components/totalSeconds.vue";
import beenOnTheSite from "@/components/beenOnTheSite.vue";
import Numbertext from "@/components/numbertext.vue";
import inputField from "@/components/inputField.vue";

export default {
  name: "WelcomeScreen",
  components: {Numbertext, Total, countdown, beenOnTheSite, inputField},
  data() {
    return {
      totalSeconds: 0,
      total: 0,
      start: 0,
      future: 0,
      now: 0,
      visible: true
    }
  },
  computed: {
    days() {
      return Math.floor(this.totalSeconds / 86400000);
    },
    hours() {
      return this.diff.getUTCHours();
    },
    minutes() {
      return this.diff.getUTCMinutes();
    },
    seconds() {
      return this.diff.getUTCSeconds();
    },
    diff() {
      return new Date(this.totalSeconds);
    },
    tts() {
      let divString = (this.totalSeconds / 1000).toString();

      if (divString.includes('.')) {
        let rest = divString.split('.');
        let endPadded = rest[1].padEnd(3, '0');

        return rest[0] + '.' + endPadded;
      } else {
        return divString;
      }
    },
    secondsOnSite() {
      return this.total + Math.floor((this.future - this.start) / 1000);
    }
  },
  methods: {
    logState() {
      let result = this.$store.getters.inputStateConv;

      Notification.requestPermission().then();

      // todo: remove
      console.log(result);

      this.$store.commit('addAlert', result);
    }
  },
  mounted() {

    localStorage.removeItem("secondsOnSite");

    let config = localStorage.getItem("sos");
    if (config) {
      this.total = parseInt(config);
    }

    this.start = Date.now();
    this.future = Date.now();

    document.addEventListener('visibilitychange', () => {
      if (document.visibilityState === "hidden") {
        this.visible = false;
        localStorage.setItem("sos", this.secondsOnSite.toString());
      } else {
        this.visible = true;
        this.total = parseInt(localStorage.getItem("sos"));
        this.start = Date.now();
        this.future = Date.now();
      }
    });

    let mainLoop = setInterval(() =>{
      if (this.visible) {
        this.now = Date.now();
        this.totalSeconds = this.$store.getters.finalDateConv - this.now;
        this.future = Date.now();
      }

      /*
      let alert = this.$store.state.alerts[Math.floor(this.totalSeconds / 1000)];
      if (alert) {
        new Notification("Time is up", {body: `${alert.total} seconds have passed`});
        this.$store.commit("clearAlert", alert.total);
      }
*/
    }, 100);
  }
}
</script>

<style lang="sass" scoped>
@import "@/assets/variables.sass"

h1
  margin: 0

#body
  display: grid
  grid-template-columns: repeat(4, 1fr)
  grid-template-rows: repeat(4, 1fr)

  place-items: center

#title
  cursor: text
  grid-column: 2 / 4
  grid-row: 1

  font-size: 2rem

  font-family: $base-font-bold
  text-align: center

#total
  grid-column: 1
  grid-row: 2

#inpf
  grid-column: 2
  grid-row: 2

#days
  grid-column: 1
  grid-row: 3

#hours
  grid-column: 2
  grid-row: 3

#minutes
  grid-column: 3
  grid-row: 3

#seconds
  grid-column: 4
  grid-row: 3

#bots
  grid-column: 4
  grid-row: 2

@media only screen and (max-height: 800px)
  #body
    grid-template-rows: repeat(4, 1fr)


@media only screen and (max-width: 600px)
  #body
    grid-template-columns: auto
    grid-template-rows: repeat(7, 1fr)

  #title
    grid-column: 1
    grid-row: 1

  #total
    grid-column: 1
    grid-row: 2

  #inpf
    grid-column: 1
    grid-row: 3

  #bots
    grid-column: 1
    grid-row: 4

  #seconds
    grid-column: 1
    grid-row: 5

  #minutes
    grid-column: 1
    grid-row: 6

  #hours
    grid-column: 1
    grid-row: 7

  #days
    grid-column: 1
    grid-row: 8


</style>