<template>

  <div id="progress">
    <div id="barOverflow">
      <div id="bar"></div>
    </div>
  </div>

</template>

<script>

export default {
  props: ["percentage"],
  name: "circularLoadingBar",
  data() {
    return {
      width: 14
    }
  },
  computed: {
    calculatedPercentage() {
      return `rotate(${45+(this.percentage*1.8)}deg)`;
    },
    elementWidth() {
      return `${this.width}rem`;
    },
    elementWidthHalf() {
      return `${Math.floor(this.width / 2)}rem`;
    }
  },

  methods: {

  },

  watch: {

  },
}

</script>

<style lang="sass" scoped>
@import "../assets/variables.sass"

#progress
  position: relative
  float: left
  text-align: center

#barOverflow
  position: relative
  overflow: hidden

  width: v-bind('elementWidth')
  height: v-bind('elementWidthHalf')

  margin-bottom: -14px

#bar
  position: absolute
  top: 0
  left: 0
  width: v-bind('elementWidth')
  height: v-bind('elementWidth')

  border-radius: 50%
  box-sizing: border-box
  border: 25px solid white

  border-bottom-color: $base-button-border
  border-right-color: $base-button-border
  transform: v-bind('calculatedPercentage')

</style>